<template>
	<div id="single-blog">
		<h1>{{blog.title}}</h1>
		<article>{{blog.content}}</article>
		<p>作者：{{blog.author}}</p>
		<p>分类：</p>
		<ul>
			<li v-for="category in blog.categories">
				{{category}}
			</li>
		</ul>
		<button @click="delereSingleBlog()">
			删除
		</button>
		<router-link to="'/edit/'+id" exact>
			编辑
		</router-link>
	</div>
</template>

<script>
	export default{
		name:"single-blog",
		data(){
			return {
				id:this.$route.params.id,
				blog:{}
			}
		},

		created(){
			this.$http.get('https://wd6216023368weqnae.wilddogio.com/posts/'+this.id+".json")
				.then(function(data){
					 console.log(data);
					 return data.json();
					 //this.blog = data.body;
				})
				.then(function(data){
					this.blog = data;
				})
		},

		methods:{
			delereSingleBlog(){
				this.$http.delete("https://wd6216023368weqnae.wilddogio.com/posts/"+this.id+".json")
					.then(response => {
						this.$router.push({path:'/'})
					})
			}
		}
	};
</script>

<style scoped>
	#single-blog{
		max-width: 960px;
		margin: 0 auto;
		padding: 20px;
		background: #ccc;
		border: 1px dotted #aaa;
	}

	button{
		display: block;
	    margin:20px 0;
	    background: crimson;
	    color: #fff;
	    border: 0;
	    border-radius: 4px;
	    padding: 14px;
	    font-size: 18px;
	    cursor:pointer;
	}
	.router-link-active{
		background: rgba(255,255,255,0.8);
		color: #444;
	}

</style>


