<template>
  <div id="add-blog">
    <h2>编辑博客</h2>
    <form v-if="!submited">
      <label>博客标题</label>
      <input type="text" v-model="blog.title" name="theme" required/>

      <label>博客内容</label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkBoxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories">
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories">
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories">
        <label>Angular4.js</label>
        <input type="checkbox" value="Angular4.js" v-model="blog.categories">
      </div>
  
      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors">{{author}}</option>
      </select>
      <button v-on:click.prevent="post" >编辑博客</button>
    </form>

    <div v-if="submited">
      <h3>博客编辑成功</h3>
    </div>


    <hr>

    <div id="preview">
      <h3>博客总览</h3>
      <p>博客标题:{{blog.title}}</p>
      <p>博客内容:</p> 
      <p>{{blog.content}}</p>
      <p>博客分类:</p>
      <ul>
        <li v-for="category in blog.categories">{{category}}</li>
      </ul>
      <p>作者：{{blog.author}}</p>
    </div>
  </div>
</template>

<script>

  export default {

    //http://jsonplaceholder.typicode.com/posts
    //https://wd6216023368weqnae.wilddogio.com/posts.json

    name: 'add-blog',

    data(){
      return{
      	id:this.$route.params.id,
        blog:{},
        authors:["ZSS","Jarry","Tony","Taylor","Jackson"],
        submited:false
      }
    },

    methods:{
      fetchData(){
      	// console.log(this.id);
      	this.$http.get('https://wd6216023368weqnae.wilddogio.com/posts/'+this.id+".json")
      		.then(response=>{
      			// console.log(reponse.body);
      			this.blog = response.body;
      		})
      },
      post:function(){
          this.$http.put('https://wd6216023368weqnae.wilddogio.com/posts/'+this.id+".json",this.blog)
            .then(function(data){
              console.log(data);
              this.submited = true;
            });
      }
    },

    created(){
    	this.fetchData();
    }

  };



</script>

<style scoped>
  #add-blog *{
    box-sizing: border-box;
  }
  #add-blog{
    margin: 20px auto;
    max-width: 600px;
    padding: 20px;
  }
    
  label{
    display: block;
    margin: 20px 0 10px;
  }

  input[type="text"],textarea,select{
    display: block;
    width: 100%;
    padding: 8px;
  }

  textarea{
    height: 200px;
  }

  #checkBoxes label{
      display: inline-block;;
      margin-top: 0;
  }

  #checkBoxes input{
    display: inline-block;
    margin-top: 10px;
  }

  button{
    display: block;
    margin:20px 0;
    background: crimson;
    color: #fff;
    border: 0;
    border-radius: 4px;
    padding: 14px;
    font-size: 18px;
    cursor:pointer;
  }

  #preview{
    padding: 10px 20px;
    border: 1px dotted #ccc;
    margin:30px 0;
  }

  h3{
    margin-top: 10px;
  }

</style>



